export default {
  data () {
    return {
      msg: 'Brand.vue',
      position: 'bottom',
      showPositionValue: false,
      searchShow: true,
      results: [],
      groupList: [
        '任务一',
        '任务二',
        '任务三',
        '任务四',
        '任务五',
        '任务六',
        '任务七',
        '任务八',
        '任务九',
        '任务一',
        '任务二',
        '任务三',
        '任务四',
        '任务五',
        '任务六',
        '任务七',
        '任务八',
        '任务九',
        '任务十'
      ],
      title: ''
    }
  },
  methods: {
    showMenus () {
      this.showPositionValue = true
    },
    sendParams: function (item) {
      this.$router.push({
        name: 'governmentInfo',
        params: {
          id: item,
          data: 'dsdsd'
        }
      })
    },
    onFocus () {
      this.searchShow = false
    },
    onBlur () {
      this.searchShow = true
    },
    resultClick (item) {
      this.sendParams(item)
    },
    getResult (val) {
      console.log('on-change', val)
      this.results = val ? this.getResults(this.value) : []
    },
    getResults: function (val) {
      console.log('on-changess')
      let str = this
      let rs = []
      for (let i = 0; i < this.groupList.length; i++) {
        if (str.groupList[i].indexOf(val) >= 0) {
          rs.push({
            title: str.groupList[i],
            other: i
          }
          )
        }
      }
      console.log(rs)
      return rs
    }

  }
}



