/* eslint-disable quotes,import/no-duplicates */
import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import Brand from '../components/Brand.vue'
import Member from '../components/Member.vue'
import Cart from '../components/HelloFromVux.vue'
import Me from '../components/Me.vue'
import governmentInfo from '../components/governmentInfo_Vux'

Vue.use(Router)

export default new Router({
  mode: 'history',
  // base: __dirname,
  // linkActiveClass: 'active', // 更改激活状态的Class值
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/brand',
      name: 'Brand',
      component: Brand
    },
    {
      path: '/member',
      name: 'Member',
      component: Member
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart
    },
    {
      path: '/me',
      name: 'Me',
      component: Me
    },
    {

      path: '/governmentInfo/:id/',
      name: 'governmentInfo',
      component: governmentInfo
    }
  ]
})



