
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import Store from './store'
import ImagVux from './ImagVux.vue'


export default {
components: {
Group,
Cell,
ImagVux
},
events: {
},
methods: {
doSomethirg: function () {
this.msg3++
},
toggleFinish: function (item) {
item.islabel = !item.islabel
},
addNew: function () {
console.log(this.newItem)
this.items.push({
label: this.newItem,
islabel: false
})
this.newItem = ''
},
listenToMyBoy: function (msg) {
this.childWords = msg
}
},
watch: {
'msg3': function (val, oldVal) {
console.log(val, oldVal)
},
items: {
handler: function (items) {
Store.save(items)
},
deep: true
}
},
data () {
return {




msg: 'Hello World!',
items: Store.fetch(),
msg3: 1,
isShow: true,
newItem: '',
childWords: ''
}
}
}



